* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.rm {
    list-style: none;
    display: inline-block;
}

.center {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    background-color: rgba(93, 102, 230, 0.781);
    align-items: center;
    font-weight: bolder;
}

.cent {
    width: 460px;
    height: 300px;
    padding: 30px;
    background-color: white;
    border: 1px solid blueviolet;
    box-shadow: 8px 8px 14px black;
    border-radius: 20px;
    overflow: hidden;
    display: grid;
    place-items: center;
}

.wd {
    width: 100%;
}

.back {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 130%;
    height: 50px;
    color: rgba(0, 0, 0, 0.863);
    text-shadow: 5px 5px 4px grey;
    font-size: 3.5rem;
    margin-bottom: 20px;
}

input {
    padding: 20px;
    height: 25px;
    overflow: hidden;
    font-weight: bold;
    outline: none;
    border: none;
    font-size: 1.2rem;
}

ol {
    margin-top: 10px;
    list-style: none;
}

ol li {
    padding: 5px;
    color: blueviolet;
    font-size: 1.2rem;
    font-weight: bolder;
    margin-left: -180px;
    overflow: hidden;
    text-align: left;
    text-transform: uppercase;
}

.bas {
    overflow: hidden;
}

.size {
    font-size: 2rem;
    padding-right: 5px;
    outline: none;
    text-decoration: none;
}

.size:hover {
    color: red;
}

textarea {
    outline: none;
    padding: 20px;
    margin: 20px;
    border: none;
}

.bton {
    width: 50px;
    height: 60px;
    background-color: white!important;
    margin-left: 230px!important;
    margin-bottom: -61px!important;
    padding: 10px!important;
    outline: none!important;
    border: none;
    color: orange!important;
    border-radius: 90px!important;
}

.bton:hover {
    color: white!important;
    background-color: orange!important;
}

.bton1 {
    width: 5px!important;
    height: 25px;
    background-color: white!important;
    margin-left: 140px!important;
    margin-bottom: -11px!important;
    padding: 10px!important;
    outline: none!important;
    border: none;
    color: orange!important;
    border-radius: 90px!important;
}

.bton1:hover {
    color: red!important;
}

.maindiv {
    height: 100vh;
    width: 100vw;
}

.tshadow {
    text-shadow: 3px 3px 5px grey;
}

.p {
    margin-left: 500px!important;
}